@font-face {
	font-family: 'YS Text';
	src: url('../fonts/YS\ Text-Regular.woff') format('woff'), /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
		url('../fonts/YS\ Text-Bold.woff') format('woff'), /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
		url('../fonts/YS\ Text-Regular.ttf') format('truetype'), /* Chrome 4+, Firefox 3.5, Opera 10+, Safari 3—5 */
		url('../fonts/YS\ Text-Bold.ttf') format('truetype'); /* Chrome 4+, Firefox 3.5, Opera 10+, Safari 3—5 */
} 

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

body {
	margin: 0;
	font-family: 'YS Text', -apple-system, 'Helvetica Neue', sans-serif;
	font-size: 18px;
	line-height: 24px;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.main-wrap {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
	width: 100%;
	min-width: fit-content;
}

.explanation-wrap {
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding: 32px;
	width: 764px;
	min-width: 764px;
	height: 573px;
	background: #fff;
	border-radius: 16px;
	border: 1px solid rgba(209, 210, 214, 0.5);
	box-sizing: border-box;
	overflow: hidden;
}
.pagination-wrap {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 20px;
}
.illustration-wrap {
	width: 100%;
	height: 100%;
}
.visualization-block {
	position: relative;
}


.dot {
	position: absolute;
	width: 8px;
	height: 8px;
	background-color: green;
	border-radius: 50%;
	&.top {
		z-index: 10;
		box-shadow: 0 0 0 1.5px #1a1b22;
	}
}

.dot-tooltip {
	position: absolute;
	top: 8px;
	left: 8px;
	background-color: #1a1b22;
	color: #fff;
	padding: 2px 8px;
	border-radius: 0 8px 8px 8px;
	font-size: 0.8em;
	white-space: nowrap;
	opacity: 0;
	display: none;
	z-index: 10;
	
	&.shown {
		opacity: 1;
		display: block;
	}
}

.axis {
	position: absolute;
	top: 305px;
	left: 0;
	display: flex;
	flex-direction: column;

	&-caption {
		align-self: flex-end;
		font-size: 0.8em;
		margin-right: 0.8em;
	}
}

.button-primary {
	display: flex;
	justify-content: center;
	padding: 13px 24px;
	font-size: 18px;
	line-height: 22px;
	font-weight: 400;
	color: #fff;
	background-color: #1a1b22;
	border: 0;
	outline: none;
	border-radius: 8px;
	cursor: default;
	transition: cursor .15s ease-out,background-color .15s ease-out;
	width: 180px;

	&:hover {
		cursor: pointer;
		background-color: #005cff;
	}
}
.button-reset {
	padding: 12px;
	display: flex;
	justify-content: center;
	font-size: 18px;
	line-height: 22px;
	font-weight: 400;
	background-color: #1a1b22;
	border: none;
	border-radius: 50%;
	outline: none;
	cursor: default;
	transition: cursor .15s ease-out,background-color .15s ease-out;

	&:hover {
		cursor: pointer;
		background-color: #005cff;
	}
}

